  .cards {
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 17px 20px;
    gap: 17px 20px;
  }
}
@media screen and (max-width: 768px) {
  .cards {
    max-width: 282px;
    margin: 36px auto 0;
    grid-template-columns: repeat(1, 1fr);
    grid-column: 20px;
  }
}
