.popup {
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  min-width: 100%;
  min-height: 100%;
  display: flex;
  visibility: hidden;
  opacity: 0;
  transition: visibility .5s, opacity .5s ease-in-out;
  align-items: center;
  justify-content: center;
}
.popup_image {
  background-color: rgba(0, 0, 0, 0.9);
}
.popup_opened {
  visibility: visible;
  opacity: 1;
}
